import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { actions, getState } from '../state';
import { Form, Button, Stack, Card, CardImg, CardBody, CardText, CardTitle } from 'react-bootstrap';
import FormInput from 'pages/components/FormInput';
import { useForm } from 'react-hook-form';
import pakeWeekend from '../bin/pakeWeekend.png';
import FormModal from 'pages/components/FormModal';

const CounterApiGrid = () => {
  const dispatch = useDispatch();
  
  const form = useForm({
    mode: "onTouched",
    reValidateMode: "onChange",
  });

  const [modalVisible, setModalVisible] = useState(false);
  const onSubmit = () => {
    const apiKey = form.getValues('apiKey')
    dispatch(actions.getList(apiKey));
  }

  useEffect(() => {
    //  
    const subscription = form.watch((value, { name, type }) => {
      console.log(">>", value, name, type);
    })
    return () => subscription.unsubscribe();
  }, [form.watch]);

  const onButtonClick = () => {
    setModalVisible(!modalVisible);
  }
  const callBackModal = (value) => {
    form.setValue('apiKey', value);
    setModalVisible(false);
  }
  const onClose = () => {
    setModalVisible(false);
  }

  return (
    <>
      <Stack gap={2}>
        <div className="p-2">
          <Form>
            <FormInput
              label="API Key"
              id="apiKey"
              horizontal={true}
              labelSize={1}
              useForm={form}
            />
          </Form>
        </div>
        <div align='center'>
          <Button type="button" onClick={onSubmit}></Button>
        </div>
        <div align={'center'}>
          <Card style={{ width: '18rem' }}>
            { <CardImg variant="top" src={pakeWeekend} /> }
            <CardBody>
              <CardTitle> </CardTitle>
              <CardText>
                   . <br />
                .<br /> 
              </CardText>
              <div>
                <Button variant="primary" onClick={onButtonClick}>API Key  </Button>
              </div>
            </CardBody>
          </Card>
        </div>
      </Stack>
      {modalVisible && (
        <FormModal
          title=""
          bodyText=""
          callBackFnc={callBackModal}
          visible={modalVisible}
          onClose={onClose}
        />
      )}
    </>
  )
}
export default CounterApiGrid;